.section {
  height: 690px;
  clip-path: polygon(0% 0%, 0% 95%, 100% 100%, 100% 0%);
  padding-top: 20px;
  background: url('../../img/background/secondScreenSomething.png');
  background-image: -webkit-image-set(url('../../img/background/secondScreenSomething.webp') 1x, url('../../img/background/secondScreenSomething.png') 1x);
  background-repeat: no-repeat;
  background-position: 100% 2%;
  background-size: 10%;
  overflow: hidden;
}

@media(min-width: 1200px) {
  .section {
    height: 886px;
    padding-top: 50px;
    background: none;
    clip-path: polygon(0% 0%, 0% 90%, 100% 100%, 100% 0%);
    position: relative;
  }
  .img {
    display: block;
    position: absolute;
    width: auto;
    height: 50%;
    left: 60%;
    bottom: 12%;
    z-index: -1;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30%;
  }
}
