* {
  margin: 0;
  user-select: none;
}
.wrapper {
  margin-bottom: 20px;
}
.wrpaperH2 {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 30px;
}
.h2 {
  font-weight: normal;
  font-size: 30px;
  text-transform: uppercase;
  color: #222222;
  line-height: 1;
  font-family: "Poiret One";
}
.h3 {
  color: #222222;
  font-size: 22px;
  font-weight: normal;
  text-align: left;
  padding: 0 10vw;
  font-family: "Poiret One";
  text-transform: uppercase;
  line-height: 1.3;
  position: relative;
}
.span {
  color: rgba(105, 201, 202, .4);
  font-size: 48px;
  display: inline-block;
  position: absolute;
  top: -27px;
  left: 7vw;
  font-family: "Open Sans";
  font-weight: bold;
}
@media(min-width: 1200px) {
  .wrapper {
    margin-bottom: 18px;
  }
  .wrpaperH2 {
    height: 100px;
    background: url('../../../img/elements/buttonsGroup.png');
    background-image: -webkit-image-set(url('../../../img/elements/buttonsGroup.webp') 1x, url('../../../img/elements/buttonsGroup.png') 1x);
    background-repeat: no-repeat;
    background-position: 40% 50%;
    margin-bottom: 18px;
    margin-top: 44px;
  }
  .h2 {
    line-height: 100px;
    font-size: 60px;
  }
  .h3 {
    text-align: left;
    font-size: 48px;
    margin-left: 100px;
    position: unset;
    padding: 0;
    transform: translate(0, 0);
    line-height: 1;
  }
  .span {
    font-size: 100px;
    transform: translate(80px, 0);
    position: unset;
  }
}
